@import "../../global.scss";

.intro{
    background-color: #000;
    display: flex;
    flex-direction: column;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    .top{
        overflow: hidden;
        position: relative;
    
        flex: 0.6;
        .wrapper{
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
                position: relative;
                bottom: 80px;
            }

            h1{
                font-size: 60px;
                margin: 10px 0;
                color: $mainColor;
                text-shadow: rgb(33, 33, 33) 3px 5px 4px;

                @include mobile{
                    font-size: 40px;
                }
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;
                display: flex;
                span{
                    font-size: inherit;
                    align-items: center;
                    justify-content: center;
                    color: $mainColor;
                    padding-left: 3px;
                }
                @include mobile{
                    font-size: 20px;
                }
            }
            
        }
        
}
    .bottom{
        flex: 0.4;
        position: relative;


        .wrapper{
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
                position: relative;
                bottom: 80px;
            }

            h1{
                font-size: 60px;
                margin: 10px 0;
                color: $mainColor;
                text-shadow: rgb(33, 33, 33) 3px 5px 4px;

                @include mobile{
                    font-size: 40px;
                }
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;
                display: flex;
                span{
                    font-size: inherit;
                    align-items: center;
                    justify-content: center;
                    color: $mainColor;
                    padding-left: 3px;
                }
                @include mobile{
                    font-size: 20px;
                }
            }
            a{
                overflow: hidden;
                position: absolute;
                bottom: 10px;
                left: 750px;
                .arrow{
                    font-size: 80px;
                    animation: arrowBlink 2s infinite;
                    color: $mainColor;
                }
                @keyframes arrowBlink {
                    100%{
                        opacity: 0;
                    }
                }
            }
            
        }
    }
}